@import url(https://fonts.googleapis.com/css2?family=Pangolin:400,500,700&display=swap);
/* prefixed by https://autoprefixer.github.io (PostCSS: v7.0.23, autoprefixer: v9.7.3) */

body {
  font-weight: 400;
  font-family: 'Pangolin', cursive;
  background: #fa709a; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#fee140),
    to(#fa709a)
  );
  background: linear-gradient(
    to bottom,
    #fee140,
    #fa709a
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  overflow-y: hidden;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app_container {
  height: 50vh;
  display: flex; 
  flex-direction: column;
}

.successfully-message {
  margin: 1rem;
  color: #045319;
  text-align: center;
}

/* prefixed by https://autoprefixer.github.io (PostCSS: v7.0.23, autoprefixer: v9.7.3) */

.todos_container {
  padding: 1.5rem;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19) !important;
}

.todos_container h2 {
  text-align: center;
  letter-spacing: 0.2rem;
}

@media (max-width: 767px) {
  .todos_container {
    padding: 0;
    padding: initial;
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
}

/* prefixed by https://autoprefixer.github.io (PostCSS: v7.0.23, autoprefixer: v9.7.3) */

.todos_container li {
  margin: 1rem;
  padding: 15px;
  font-size: 18px;
  text-align: left;
  list-style-type: none;
  text-transform: capitalize;
  -webkit-transition: 0.5s all ease-in;
  transition: 0.5s all ease-in;
}

.todos_container li:hover {
  cursor: pointer;
  box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19) !important;
}

.todos_container li .times-icon {
  float: right;
  margin-left: 2rem;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.todos_container li .times-icon:hover {
  cursor: pointer;
  color: #c31432;
}

/* prefixed by https://autoprefixer.github.io (PostCSS: v7.0.23, autoprefixer: v9.7.3) */

.formAddTodo {
  text-align: center;
  padding: 15px;
}

.formAddTodo input {
  border: 1px solid #000;
  padding: 7px 10px;
}

.formAddTodo .btn-default {
  background: #ff7345;
  border-radius: 5px;
  padding: 8px 15px;
}

.formAddTodo,
.formAddTodo input,
.formAddTodo .btn-default {
  -webkit-transition: 0.6s all ease-out;
  transition: 0.6s all ease-out;
  font-size: 18px;
  margin-right: 10px;
}

.formAddTodo:hover,
.formAddTodo input:hover,
.formAddTodo .btn-default:hover {
  cursor: pointer;
  box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19) !important;
}

@media (max-width: 767px) {
  .formAddTodo {
    display: flex;
  }
}

@media (min-width: 320px) and (max-width: 374pxa) {
  .formAddTodo {
    padding: 0;
  }
}

/* prefixed by https://autoprefixer.github.io (PostCSS: v7.0.23, autoprefixer: v9.7.3) */

.footer {
  text-align: center;
  margin-top: 5rem;
}

.footer h1 {
  font-size: 22px;
}

.footer a {
  color: #000;
  text-decoration: none;
  -webkit-transition: 0.4s all ease;
  transition: 0.4s all ease;
}

.footer a:hover {
  color: #240b36;
}

